.page-contact {
  background: gray;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 536px;
  min-width: var(--base-min-width); }
  .page-contact__content {
    margin: 100px 0 23px;
    color: #fff; }
  .page-contact__descr {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }

.contact-content {
  text-transform: uppercase;
  color: #fff;
  margin: 0 0 36px; }
  .contact-content__h1 {
    font-family: playfairdisplay;
